<template>
    <v-container fluid>
        <v-toolbar color=#ebac0c fixed>
            <v-menu offset-y>
                <template v-slot:activator="{on}">
                    <v-app-bar-nav-icon
                        v-on="on"
                    ></v-app-bar-nav-icon>
                </template>
                <v-list>
                    <v-list-item
                        v-for="(item, i) in items"
                        :key="i"
                        @click="openTitle(item.title)"
                    >
                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>
            <v-toolbar-title>Adgame</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn
                v-if="this.$store.state.token==''"
                @click="openLogin" 
                text
            >Войти</v-btn>
            <v-btn
                v-if="this.$store.state.token!=''"
                text
                @click="logout"
            >Выйти</v-btn>
            <v-btn
                @click="openRegister"
                color=#ebac0c
            >Регистрация</v-btn>
        </v-toolbar>
        <RegModal
            v-if="registration"
            @openLogin="openLogin"
            @closeRegister="closeRegister"
        />
        <LogModal
            v-if="login"
            @closeLogin="closeLogin"
            @loginAccept="loginAccept"
        />
    </v-container>
</template>

<script>
import RegModal from '@/components/RegModal.vue';
import LogModal from '@/components/LogModal.vue';

export default {
    name: 'NavigationBar',
    components:{
        RegModal,
        LogModal,
    },
    data: () => ({
        items: [
            { title: 'О проекте' },
            { title: 'К игре'}
        ],
        registration: false,
        login: false,
        token: '',
    }),
    methods:{
        logout: function(){
            this.$store.commit('setToken', '');
        },
        openTitle: function(title){
            if (title == "О проекте"){
                this.$router.push({ name: 'about' });
            } else if (title == "К игре"){
                this.$router.push({ name: 'game-start' });
            }
        },
        openLogin() {
            this.registration = false;
            this.login = true;
        },
        openRegister() {
            this.login = false;
            this.registration = true;
        },
        closeRegister(){
            this.registration = false;
            this.login =  false;
        },
        closeLogin(){
            this.registration = false;
            this.login =  false;
        },
        loginAccept(token){
            this.registration = false;
            this.login =  false;
            this.token = token;
            this.$store.commit('setToken', this.token);
        }
    }
};
</script>

