<template>
  <div class="modal-backdrop">
    <div class="modal">
      <header class="modal-header">
        Вход в учетную запись
        <button type="button" class="btn-close" @click="closeLog">X</button>
      </header>
      <section class="modal-body">
        <slot name="body">
          <v-text-field
            v-model="username"
            label="Username"
            placeholder="Username"
            outlined
            :rules="[rules.required]"
          ></v-text-field>
          <v-text-field
            v-model="password"
            label="Password"
            placeholder="Password"
            outlined
            :rules="[rules.required]"
          ></v-text-field>
          <h4 class="error-msg">{{ msg }}</h4>
        </slot>
      </section>
      <footer class="modal-footer">
        <button type="button" class="btn-func" @click="loginUser">
          Войти
        </button>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LogModal',
  data: () => ({
    password: '',
    username: '',
    email: 'ex@ex.com',

    token: '',

    rules: {
      required: value => !!value || 'Заполните поле',
      min: v => v.length >= 8 || 'Минимум 8 символов'
    },

    msg: ''
  }),
  methods: {
    closeLog() {
      this.$emit('closeLogin'); // throw to parent function call
    },
    loginUser() {
      this.msg = '';
      if (this.username != '' && this.password != '') {
        this.loginIn();
      } else {
        this.msg = 'Заполните поля';
      }
    },
    loginIn: async function(){
            let payload = {
                "username": this.username,
                "password": this.password,
            };
            let url = "https://morning-thicket-20719.herokuapp.com";
            let response = await fetch(url + "/api/login/",
            {
                method: "POST",
                headers: new Headers({'content-type': 'application/json'}),
                body: JSON.stringify(payload)
            });

            if (response.ok) { // if HTTP-status is 200-299
                let jsonResp = await response.json();
                this.token = jsonResp.token;
                this.$emit('loginAccept', this.token);
                this.notRegLog = false;
                this.login = false;
                this.registration = false;

            } else {
                this.msg = 'Неверный username или password';
            }   
        }
  }
};
</script>

<style>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #ffffff;
  box-shadow: 20px 20px 20px 20px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
}

.modal-header,
.modal-footer {
  padding: 15px;
  display: flex;
}

.modal-header {
  border-bottom: 1px solid #eeeeee;
  color: #ebac0c;
  justify-content: space-between;
}

.modal-footer {
  border-top: 1px solid #eeeeee;
  justify-content: flex-end;
}

.modal-body {
  position: relative;
  padding: 10px 10px 10px 10px;
}

.btn-close {
  border: none;
  font-size: 15px;
  cursor: pointer;
  font-weight: bold;
  color: #ebac0c;
  background: transparent;
}

.btn-func {
  color: white;
  background: #ebac0c;
  margin: 10px 20px 10px 20px;
  padding: 10px;
  border: 1px solid #ebac0c;
  border-radius: 2px;
}

.error-msg {
  color: #d10010;
}
</style>
